.App {
  text-align: center;
  background: linear-gradient(120deg, #84fab0, #8fd3f4);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
}

input, button {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 5px;
}

button {
  background-color: #84fab0;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}

button:hover {
  background-color: #8fd3f4;
}
